import React, { FC, Suspense, lazy, useState, useCallback, useEffect } from "react";
import { Switch, Route, useLocation } from "react-router-dom";
import { HamburgerElastic } from 'react-animated-burgers'
import { LoadingFrame, AppHeader, AppFooter, MobileMenu } from "../components/Shared";
import { Layout } from 'antd';
const { Content, Sider } = Layout;

const Home = lazy(() => import("./Home/Home"));
const Overview = lazy(() => import("./Overview/Overview"));
const Bounty = lazy(() => import("./Bounty/Bounty"));
const Contact = lazy(() => import("./Contact/Contact"));
const Faq = lazy(() => import("./Faq/Faq"));
const Token = lazy(() => import("./Token/Token"));

const Routes: FC = () => {
  const [isActive, setIsActive] = useState(false)

  const toggleButton = useCallback(
    () => setIsActive(prevState => !prevState),
    [],
  );

  const toggleMenu = useCallback(
    () => setIsActive(prevState => prevState ? false : prevState),
    [],
  );

  const location = useLocation();
    
  useEffect(
    () => toggleMenu(),
    [location, toggleMenu]
  );

  return (
    <Layout>
      <Layout className={ isActive ? "sider-active" : ""}>
        <AppHeader />
        <Content>
          <Suspense fallback={<LoadingFrame />}>
            <Switch>
              <Route path="/" exact children={<Home />} />
              <Route
                path="/overview"
                exact
                children={<Overview />}
              />
              <Route
                path="/bounty"
                exact
                children={<Bounty />}
              />
              <Route
                path="/contact"
                exact
                children={<Contact />}
              />        
              <Route
                path="/faq"
                exact
                children={<Faq />}
              />
              <Route
                path="/token"
                exact
                children={<Token />}
              />
            </Switch>
          </Suspense>
        </Content>
        <AppFooter />
      </Layout>
      <Sider
        className={isActive ? "mobile-menu mobile-menu--isActive" : "mobile-menu"}
        trigger={
          <HamburgerElastic buttonStyle={{ backgroundColor: "none" }} buttonWidth={30} barColor="#fff" className="mobile-menu__trigger" {...{ isActive }} />
        }
        onCollapse={() => toggleButton()}
        collapsed={!isActive}
        breakpoint="sm"
        collapsedWidth="0"
        defaultCollapsed={true}
        width={"100vw"}
      >
        <MobileMenu />
      </Sider>
    </Layout>
  );
};


export default Routes;

